@-webkit-keyframes pulsate {
  0% {
    -webkit-transform: scale(1, 1);
    opacity: 0.9;
  }
  50% {
    -webkit-transform: scale(1.2, 1.2);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: scale(1, 1);
    opacity: 0.9;
  }
}
@-webkit-keyframes pulsate-inner {
  0% {
    -webkit-transform: scale(1, 1);
    opacity: 0.9;
  }
  50% {
    -webkit-transform: scale(1.3, 1.3);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: scale(1, 1);
    opacity: 0.9;
  }
}
