.authorizations {
  margin-top: 25px;
}
.authorizations .authorization-item {
  margin-bottom: 25px;
}
.authorizations .authorization-item .title-and-paragraph h5 {
  font-size: 18px;
  margin-top: 10px;
}
.primary-description ul {
  list-style: none;
}
.primary-description ul li {
  padding: 0 0 0 32px;
  background: url('../images/green-tick.png') no-repeat left center;
  line-height: 20px;
  margin: 12px 0;
}
.related-technologies .technology {
  background: white;
  height: 310px;
  padding: 7.5px;
  text-align: center;
}
.related-technologies .technology .technology-wrapper {
  padding: 25px;
  background: #f2f2f2;
}
.related-technologies .technology .image {
  height: 140px;
  width: 100%;
  background-position: center center;
  background-size: cover;
  margin-bottom: 10px;
}
.related-technologies .technology h5 {
  font-size: 18px;
}
.downloads .download-item {
  background: white;
  height: 100px;
  margin-bottom: 25px;
}
.downloads .download-item img {
  display: block;
  float: left;
  height: 100px;
  margin-right: 25px;
}
.downloads .download-item .title-and-link {
  padding: 30px 0 0 30px;
}
.downloads .download-item .title-and-link h4 {
  font-weight: bold;
  font-size: 16px;
}
.job-testimonials .job-testimonial-item {
  float: left;
  padding: 25px 0 0 0;
}
.job-testimonials .job-testimonial-item .job-testimonial-wrapper {
  background: white;
}
.job-testimonials .job-testimonial-item .job-testimonial-wrapper:hover {
  cursor: pointer;
  background: #d6d6d6;
}
.job-testimonials .job-testimonial-item .job-testimonial-wrapper > div {
  height: 250px;
  padding: 20px;
  position: relative;
}
.job-testimonials .job-testimonial-item .job-testimonial-wrapper > div h3 {
  font-size: 22px;
}
.job-testimonials .job-testimonial-item .job-testimonial-wrapper > div h3 span.job-title {
  position: relative;
  top: -5px;
  display: block;
  font-size: 14px;
  color: #b3b3b3;
}
.job-testimonials .job-testimonial-item .job-testimonial-wrapper > div quote {
  display: block;
  margin-bottom: 12.5px;
}
.job-testimonials .job-testimonial-item .job-testimonial-wrapper > div .btn {
  position: absolute;
  bottom: 25px;
  right: 25px;
}
.job-testimonials .job-testimonial-horizontal {
  width: 66%;
}
.job-testimonials .job-testimonial-horizontal .job-testimonial-wrapper {
  height: 250px;
}
.job-testimonials .job-testimonial-horizontal .job-testimonial-wrapper > div {
  width: 66%;
  float: right;
}
.job-testimonials .job-testimonial-horizontal .job-testimonial-wrapper .image {
  width: 34%;
  height: 250px;
  float: left;
  background-size: cover;
  background-position: center center;
}
.job-testimonials .job-testimonial-vertical {
  width: 34%;
  float: right;
  padding: 25px 0px 0px 25px;
}
.job-testimonials .job-testimonial-vertical .job-testimonial-wrapper {
  height: 525px;
}
.job-testimonials .job-testimonial-vertical .job-testimonial-wrapper > div {
  height: 50%;
}
.job-testimonials .job-testimonial-vertical .job-testimonial-wrapper > div .btn {
  bottom: 12px;
}
.job-testimonials .job-testimonial-vertical .job-testimonial-wrapper .image {
  width: 100%;
  height: 250px;
  background-size: cover;
  background-position: center center;
}
.job-testimonials .job-testimonial-vertical-left {
  float: left;
  padding: 25px 25px 25px 0px;
  width: 34%;
}
@media screen and (max-width: 1215px) {
  .job-testimonials .job-testimonial-horizontal,
  .job-testimonials .job-testimonial-vertical,
  .job-testimonials .job-testimonial-vertical-left {
    width: 100%;
  }
  .job-testimonials .job-testimonial-vertical .job-testimonial-wrapper {
    height: 250px;
  }
  .job-testimonials .job-testimonial-vertical,
  .job-testimonials .job-testimonial-vertical-left {
    padding: 25px 0 0 0;
    float: left;
  }
  .job-testimonials .job-testimonial-vertical .job-testimonial-wrapper > div {
    width: 66%;
    float: left;
  }
  .job-testimonials .job-testimonial-vertical .job-testimonial-wrapper .image {
    width: 34%;
    float: left;
  }
}
@media screen and (max-width: 670px) {
  .job-testimonials .job-testimonial-horizontal,
  .job-testimonials .job-testimonial-vertical,
  .job-testimonials .job-testimonial-vertical-left {
    width: 100%;
  }
  .job-testimonials .job-testimonial-vertical,
  .job-testimonials .job-testimonial-vertical-left {
    padding: 25px 0 0 0;
  }
  .job-testimonials .job-testimonial-horizontal .job-testimonial-wrapper {
    height: 525px;
    position: relative;
  }
  .job-testimonials .job-testimonial-horizontal .job-testimonial-wrapper .image,
  .job-testimonials .job-testimonial-vertical .job-testimonial-wrapper .image {
    width: 100%;
    height: 30%;
    background-size: cover;
    background-position: center center;
    position: absolute;
    top: 0;
    left: 0;
  }
  .job-testimonials .job-testimonial-horizontal .job-testimonial-wrapper > div,
  .job-testimonials .job-testimonial-vertical .job-testimonial-wrapper > div {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 70%;
    padding: 20px;
  }
}
